<template>
   <div class="m-product-categroy">
     <dl class="classic">
       <dt>分类</dt>
       <dt>全部</dt>
       <dd v-for="(item) in classicList" :key="item.type">
         <m-select :name="item.title" :list="item.subList"></m-select>
       </dd>
     </dl>
      <dl class="classic">
       <dt>区域</dt>
       <dt>全部</dt>
       <dd v-for="(item) in areaList" :key="item.type">
         <m-select :name="item.title" :list="item.subList"></m-select>
       </dd>
     </dl>
   </div>
</template>
<script>
import MSelect from './select.vue'
import api from "@/api/index.js"
export default {
  data() {
    return {
      classicList: [
        {
          title: "美食",

          type: "food",

          subList: [
            {
              name: "日本菜",

              id: "Japan"
            },
            {
              name: "中国菜",

              id: "China"
            },
            {
              name: "美国菜",

              id: "Amer"
            }
          ]
        },
        {
          title: "酒店住宿",

          type: "hotal",

          subList: [
            {
              name: "温泉酒店",

              id: "hot_spring"
            },
            {
              name: "舒适酒店",

              id: "com_spring"
            },
            {
              name: "经济酒店",

              id: "lel_spring"
            }
          ]
        }
      ],
     areaList:[{

        "title": "推荐商圈",

        "subList": [{

            "name": "望京",

            "id": 120000

        }, {

            "name": "昌平",

            "id": 12222

        }]

    }]
    };
  },
  created() {
   api.getClassify().then(res =>{
        console.log(res)
        // console.log(res.data.data[0])
        this.classicList = [res.data.data[0]];
        this.areaList = [res.data.data[1]];
        // console.log([res.data.data[0]])

   })
  },
  components:{
    MSelect
  }
};
</script>




