<template>
  <div class="search-panel">
    <el-row class="m-header-searchbar">
      <el-col :span="3" class="left">
        <img src="//s0.meituan.net/bs/fe-web-meituan/e5eeaef/img/logo.png" alt="美团">
      </el-col>
      <el-col :span="15" class="center">
        <div class="wrapper">
          <!-- <el-input @focus="focus" @blur="blur" v-model="searchWord" placeholder="请输入内容" class="el-input"></el-input> -->
          <input
            @focus="focus"
            @blur="blur"
            @input ="input"
            type="text"
            class="el-input"
            v-model="searchWord"
            placeholder="请输入内容"
          >
          <el-button type="primary" icon="el-icon-search" class="el-button">
            <!-- <i class="el-icon-search"></i> -->
          </el-button>
          <dl class="hotPlace" v-if="isHotPlace">
            <dt>热门搜索</dt>
            <dd v-for="(item,index) in hotPlaceList" :key="index">
              <router-link :to="{name : 'goods',params:{name: item}}">{{item}}</router-link>
            </dd>
            <!-- <dd>
          <router-link to="/">湖南省森林植物园</router-link>
        </dd>
         <dd>
          <router-link to="/">长沙失恋博物馆</router-link>
        </dd>
         <dd>
          <router-link to="/">长沙生态动物园</router-link>
        </dd>
         <dd>
          <router-link to="/">长沙世界之窗</router-link>
            </dd>-->
          </dl>
          <dl class="searchList" v-if="isSearchList">
            <dd v-for="(item,index) in searchList" :key="index">
              <router-link :to="{name: 'goods',params:{name:item}}">{{item}}</router-link>
            </dd>
            <!-- <dd><router-link to="/s">火锅</router-link></dd>
        <dd><router-link to="/s">火锅自助餐</router-link></dd>
            <dd><router-link to="/s">火锅 重庆</router-link></dd>-->
          </dl>
        </div>
        <p class="suggest">
          <!-- <a
            href="https://www.meituan.com/s/%E6%B9%96%E5%8D%97%E7%9C%81%E6%A3%AE%E6%9E%97%E6%A4%8D%E7%89%A9%E5%9B%AD"
          >湖南省森林植物园</a>
          <a
            href="https://www.meituan.com/s/%E9%95%BF%E6%B2%99%E5%A4%B1%E6%81%8B%E5%8D%9A%E7%89%A9%E9%A6%86"
          >长沙失恋博物馆</a>
          <a
            href="https://www.meituan.com/s/%E9%95%BF%E6%B2%99%E7%94%9F%E6%80%81%E5%8A%A8%E7%89%A9%E5%9B%AD"
          >长沙生态动物园</a>
          <a
            href="https://www.meituan.com/s/%E9%95%BF%E6%B2%99%E4%B8%96%E7%95%8C%E4%B9%8B%E7%AA%97"
          >长沙世界之窗</a>
          <a href="https://www.meituan.com/s/%E7%82%AD%E6%B2%B3%E5%8D%83%E5%8F%A4%E6%83%85">炭河千古情</a>
          <a
            href="https://www.meituan.com/s/%E9%95%BF%E6%B2%99%E6%A2%85%E6%BA%AA%E6%B9%96%E7%92%80%E7%92%A8%E6%98%9F%E7%A9%BA%E8%89%BA%E6%9C%AF%E9%A6%86"
          >长沙梅溪湖璀璨星空艺术馆</a>-->
          <a href="#" v-for="(item,index) in suggest" :key="index">{{item}}</a>
        </p>
      </el-col>
    </el-row>
  </div>
</template>
<script>
// import axios from "@/axios.js";
import api from '@/api/index.js'
export default {
  data() {
    return {
      searchWord: "",

      isFocus: false,
      hotPlaceList: ["湖南省森林植物园", "长沙失恋博物馆", "长沙生态动物园"],
      searchList: ["火锅", "火锅自助餐", "火锅 重庆"],
      suggest: [
        "湖南省森林植物园",
        "长沙失恋博物馆",
        "长沙梅溪湖璀璨星空艺术馆",
        "长沙世界之窗"
      ]
    };
  },
  created() {

      api.getSearchHotWord().then(res => {
        // console.log(res);
        this.hotPlaceList = res.data.data.slice(0,5);
        this.suggest = res.data.data.slice(0,5);
      });
  },
  computed: {
    isHotPlace: function() {
      return this.isFocus && !this.searchWord;
    },
    isSearchList: function() {
      return this.isFocus && this.searchWord;
    }
  },
  watch: {
    "$route.params.name": function() {
      this.searchWord = this.$route.params.name;
    }
  },
  methods: {
    focus() {
      this.isFocus = true;
    },
    blur() {
      // 每次点击 的时候延迟 不然点击不上
      let self = this;
      setTimeout(() => {
        self.isFocus = false;
      }, 200);
    },
    input() {
      // console.log(this.searchWord)
      let val = this.searchWord;
      api.getSearchWord().then(res =>{
        // console.log(res);
        this.searchList = res.data.data.list.filter((item,index) =>{
          return item.indexOf(val) > -1;
        })
      })
    }
  }
};
</script>
<style lang="scss">
// @import "@/assets/css/public/header/index.scss";
@import "@/assets/css/public/header/search.scss";
</style>



