<template>
  <div class="like-wrapper">
    <div class="recomment">
      <div class="recomment-box">
        <div class="top-box">
          <div>猜你喜欢</div>
        </div>
        <div class="recomment-list">
          <a href="#" v-for="(item,index) in like" :key="index">
            <div class="card">
              <div class="img-box">
                <img :src="item.imgUrl" alt="图片">
              </div>
              <div class="info-box">
                <div class="title">{{item.title}}</div>
                <div>
                  <span>
                    <el-rate
                      v-model="item.score"
                      disabled
                      
                      text-color="#ff9900"
                      score-template="{value}"
                    ></el-rate>
                  </span>
                  <span> {{item.commentNum}}人评价</span>
                </div>
                <div class="area">
                  <span>{{item.areaName}}</span>
                </div>
                <div class ="price">
                  <span v-if="item.lowPrice">￥{{item.lowPrice}}起</span>
                </div>
              </div>
            </div>
          </a>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      like: [
        {
          itemId: "2412338",
          title: "金真子纸上烤肉（双桥店）",
          imgUrl:
            "https://p0.meituan.net/mogu/4f945b9450e4622c4853d8a3a64a37ae46740.jpg@190w_106h_1e_1c",
          score: "4.5",
          consumeNum: null,
          areaName: "双桥",
          lowPrice: "58.0",
          saleNum: null,
          commentNum: 16449,
          detailUrl: "",
          firstCate: [1, 20632, 20426, 20557],
          avgPrice: 59
        },
         {
          itemId: "2412338",
          title: "金真子纸上烤肉（双桥店）",
          imgUrl:
            "https://p0.meituan.net/mogu/4f945b9450e4622c4853d8a3a64a37ae46740.jpg@190w_106h_1e_1c",
          score: "4.5",
          consumeNum: null,
          areaName: "双桥",
          lowPrice: "58.0",
          saleNum: null,
          commentNum: 16449,
          detailUrl: "",
          firstCate: [1, 20632, 20426, 20557],
          avgPrice: 59
        }
      ]
    };
  }
};
</script>




