<template>
  <div class="layout-default">
    <el-container class="el-container is-vertival">
      <el-header class="el-header" height="100%">
        <my-header></my-header>
      </el-header>
      <el-main class="el-main">
        <router-view></router-view>
      </el-main>
      <el-footer class="el-footer" height="">
        <my-footer></my-footer>
      </el-footer>
    </el-container>
  </div>
</template>
<script>
import MyHeader from '@/components/header/index.vue'
import MyFooter from '@/components/footer/index.vue'
export default {
  components: {
    MyHeader,
    MyFooter
  }
}
</script>
<style lang="scss">
@import "@/assets/css/public/layout.scss";
</style>



