<template>
  <div class="page-login">
    <div class="login-header">
      <a href="//ch.meituan.com" class="logo"></a>
    </div>
    <div class="login-panel">
      <div class="banner">
        <img
          src="//s0.meituan.net/bs/file/?f=fe-sso-fs:build/page/static/banner/www.jpg"
          width="480"
          height="370"
          alt="美团网"
        >
      </div>
      <div class="form">
        <h4 v-if="error" class="tips">{{error}}</h4>
        <p>
          <span>账户登录</span>
        </p>
        <el-input
          v-model="userName"
          placeholder="手机号/用户名/邮箱"
          prefix-icon="profile"
          :class="{error: error && !userName}"
        ></el-input>
        <el-input
          placeholder="请输入密码"
          v-model="password"
          show-password
          prefix-icon="password"
          :class="{error: error && !password}"
        ></el-input>
        <div class="foot">
          <a href="#">忘记密码?</a>
        </div>
        <el-button type="primary" class="btn-login" @click="submit">登录</el-button>
        <p class="reg">
          <span>还没有账号？</span>
          <router-link :to="{name:'register'}">免费注册</router-link>
        </p>
      </div>
    </div>
    <footer>
         <ul >
               <li v-for="(item, index) in list" :key="index"><a href="#" style="text-decoration:none">{{item}}</a></li>
            <!-- <li><a href="#" style="text-decoration:none">关于美团</a></li>
            <li><a href="#" style="text-decoration:none">加入我们</a></li>
            <li><a href="#" style="text-decoration:none">商家入驻</a></li>
            <li><a href="#" style="text-decoration:none">帮助中心</a></li>
            <li><a href="#" style="text-decoration:none">美团手机版</a></li> -->
        </ul>
        <p>
            ©2019 美团网团购 meituan.com 京ICP证070791号 京公网安备11010502025545号
        </p>
    </footer>
       
  </div>
</template>
<script>
import api from "@/api/index.js"
export default {
  data() {
    return {
      userName: "",
      password: "",
      error: "",
      list:['关于美团','加入我们','商家入驻','帮助中心','美团手机版']
    };
  },
  methods: {
    submit() {
      //   console.log(this.userName,this.password)
      if (!this.userName) {
        this.error = "请输入账号";
        return false;
      }
      if (!this.password) {
        this.error = "请输入密码";
        return false;
      }
        // 发送 给后端的数据 都在 名为params的属性下面
      api.login({
          params:{
              userName:this.userName,
              password:this.password
          }
      }).then(res =>{
               if(res.data.status == 'success'){
                   this.$router.push({name:'index'});
                   this.$store.commit('setUserName',this.userName)
               }else{
                   this.error = res.data.msg;
               }
      })
    }
  }
};
</script>

<style lang="scss">
@import "@/assets/css/login/index.scss";
</style>



